/* UserMapModal.css */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  width: 80%;
  height: 80%;
  overflow: auto;
}

.close-btn {
  float: right;
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
}